import React from 'react';
import './App.css';
import { Route, Switch } from "react-router-dom";
import Home from './Components/Pages/Accueil/Index';
import Login from './Components/Pages/Login/Login';
import { Show } from './Components/Pages/Profil/Show';
import { Modifier } from './Components/Pages/Profil/Modifier';
import { Password } from './Components/Pages/Profil/Password';
import Inscription from './Components/Pages/Inscription/Inscription';
import MyEvent from './Components/Pages/MesEvents/MyEvent';
import Linprog from './Components/Linprog';
import { Commande } from './Components/Pages/Confirmation/ContentCom';
import { Lieux } from './Components/Pages/Lieux/Lieux';
import { Menus } from './Components/Pages/Menus/Menus';
import { Boissons } from './Components/Pages/Boissons/Boissons';
import { Extras } from './Components/Pages/Extras/Extras';
import { Communication } from './Components/Pages/Communication/Communication';
import { Organisation }  from './Components/Pages/CreateEvent/Index';
import { Participants } from './Components/Pages/Participants/Index';
import { Tickets } from './Components/Pages/Ticket/Index';
import { CheckIn } from './Components/Pages/CheckIn/CheckIn';
import Error from './Components/Pages/404/404';
import { ProtectedRoute } from './Components/ProtectedRoute';
import { useDispatch, useSelector } from "react-redux";
import auth from './Components/Auth';

export default function App() {
 const [state, setState] = React.useState(true);
 const items = useSelector(state => state.addedItems);
 React.useEffect(() =>{
    setTimeout(() => {
      setState(false)
    }, 3000);
  });

  return (
    <div className="App">
      <Switch>
        {state ? <Linprog /> : null}
        <ProtectedRoute exact path="/evenements/organisation" component={Organisation} />
        <ProtectedRoute exact path="/evenements/tickets" component={Tickets} />
        <ProtectedRoute exact path="/evenements/commande" component={Commande} />
        <ProtectedRoute exact path="/check-in" component={CheckIn} />
        <ProtectedRoute exact path="/evenements/lieux" component={Lieux} />
        <ProtectedRoute exact path="/evenements/menus" component={Menus} />
        <ProtectedRoute exact path="/evenements/boissons" component={Boissons} />
        <ProtectedRoute exact path="/evenements/communication" component={Communication} />
        <ProtectedRoute exact path="/evenements/extras" component={Extras} />
        <ProtectedRoute exact path="/evenements/participants" component={Participants} />
        <Route exact path="/inscription" component={Inscription} />
        <ProtectedRoute exact path="/monprofil" component={Show} />
        <ProtectedRoute exact path="/monprofil/modifier" component={Modifier} />
        <ProtectedRoute exact path="/monprofil/password" component={Password} />
        <ProtectedRoute exact path="/mes_evenements" component={MyEvent} />
        <ProtectedRoute exact path="/" component={Home} />
        {/* <Route exact path="/" component={Login} /> */}
        <ProtectedRoute path="*" component={Error} />
      </Switch>
    </div>
  );
}