import React from 'react';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Typography from "@material-ui/core/Typography";
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { Formik } from "formik";
import { useHistory } from 'react-router-dom';
import * as Yup from "yup";
import { FormHelperText } from "@material-ui/core";

export const Ticket = () => {
    const useStyles = makeStyles(theme => ({
        root: {
            '& > *': {
                margin: theme.spacing(1),
                width: 280,
            },
        },
        height: {
            minHeight: '100vh',
        },
        padding: {
            paddingTop: '40px',
            paddingBottom: '40px',
        },
        field: {
            margin: theme.spacing(1),
            minWidth: 280,
        },
        button: {
            marginTop: theme.spacing(1),
        },
        formControl: {
            margin: theme.spacing(1),
            minWidth: 230,
        },
    }));
    const classes = useStyles();
    const history = useHistory();
    const inputLabel = React.useRef(null);
    const [labelWidth, setLabelWidth] = React.useState(0);
    React.useEffect(() => {
        setLabelWidth(inputLabel.current.offsetWidth);
    }, []);

    return (
        <div>
            <Typography variant="h6">Créer des tickets</Typography>
            <br></br>
            <Formik
                initialValues={{
                    type: "Payant",
                    prix: "",
                    quantity: "",
                    date_debut: "",
                    date_fin: "",
                }}
                onSubmit={(values, { setSubmitting }) => {
                    setTimeout(() => {
                        history.push("/evenements/participants");
                        setSubmitting(false);
                    }, 500);
                }}
                validationSchema={Yup.object().shape({
                    type: Yup.string().required("Ce champ est obligatoire."),
                    prix: Yup.string().required("Ce champ est obligatoire."),
                    quantity: Yup.string().required("Ce champ est obligatoire."),
                    date_debut: Yup.string().required("Ce champ est obligatoire."),
                    date_fin: Yup.string().required("Ce champ est obligatoire."),
                })}
            >
                {props => {
                    const {
                        values,
                        touched,
                        errors,
                        isSubmitting,
                        handleChange,
                        handleBlur,
                        handleSubmit
                    } = props;
                    return (
                        <form
                            onSubmit={handleSubmit}
                            noValidate
                            autoComplete="off"
                        >
                            <FormControl variant="outlined" className={classes.formControl}>
                                <InputLabel ref={inputLabel} htmlFor="outlined-type-native-simple">
                                    Type du ticket
                                </InputLabel>
                                <Select
                                    native
                                    error={errors.type && touched.type && true}
                                    value={values.type}
                                    name="type"
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    labelWidth={labelWidth}
                                    variant="outlined"
                                    helperText={
                                        errors.type &&
                                        touched.type && (
                                            <FormHelperText error>
                                                {errors.type}
                                            </FormHelperText>
                                        )
                                    }
                                >
                                    <option value="Payant">Payant</option>
                                    <option value="Gratuit">Gratuit</option>
                                    {/* <option value="Don">Don</option> */}
                                </Select>
                            </FormControl>
                            <FormControl variant="outlined" className={classes.formControl}>
                                <TextField
                                    error={errors.prix && touched.prix && true}
                                    name="prix"
                                    label="Prix du ticket"
                                    type="text"
                                    value={values.prix}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    variant="outlined"
                                    helperText={
                                        errors.prix &&
                                        touched.prix && (
                                            <FormHelperText error>
                                                {errors.prix}
                                            </FormHelperText>
                                        )
                                    }
                                    disabled={values.type == 'Gratuit' ? true : false}
                                />
                            </FormControl>
                            <FormControl variant="outlined" className={classes.formControl}>
                                <TextField
                                    error={errors.quantity && touched.quantity && true}
                                    name="quantity"
                                    label="Quantité"
                                    type="text"
                                    value={values.quantity}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    variant="outlined"
                                    helperText={
                                        errors.quantity &&
                                        touched.quantity && (
                                            <FormHelperText error>
                                                {errors.quantity}
                                            </FormHelperText>
                                        )
                                    }
                                />
                            </FormControl>
                            <FormControl variant="outlined" className={classes.formControl}>
                                <TextField
                                    error={errors.date_debut && touched.date_debut && true}
                                    name="date_debut"
                                    id="outlined-number"
                                    label="Début de la vente"
                                    type="date"
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                    variant="outlined"
                                    value={values.date_debut}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    helperText={
                                        errors.date_debut &&
                                        touched.date_debut && (
                                            <FormHelperText error>
                                                {errors.date_debut}
                                            </FormHelperText>
                                        )
                                    }
                                />
                            </FormControl>
                            <FormControl variant="outlined" className={classes.formControl}>
                                <TextField
                                    error={errors.date_fin && touched.date_fin && true}
                                    name="date_fin"
                                    id="outlined-number"
                                    label="Fin de la vente"
                                    type="date"
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                    variant="outlined"
                                    value={values.date_fin}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    helperText={
                                        errors.date_fin &&
                                        touched.date_fin && (
                                            <FormHelperText error>
                                                {errors.date_fin}
                                            </FormHelperText>
                                        )
                                    }
                                />
                            </FormControl>
                            <div className={classes.button}>
                                <Button
                                    className={classes.backButton}
                                    onClick={() => history.push('/evenements/organisation')}
                                >
                                    Retour
                                      </Button>
                                <Button variant="contained" type="submit"
                                    disabled={isSubmitting}
                                    color="primary" >
                                    Suivant
                                      </Button>
                            </div>
                        </form>
                    );
                }}
            </Formik>
        </div>
    );
}